import React from 'react'

import { graphql, StaticQuery } from "gatsby"

const Socials = () => (
    <StaticQuery
        query={graphql`
            query {
                instagram: file(relativePath: { eq: "modules/instagram.svg" }) {
                    publicURL
                    name
                }
                facebook: file(relativePath: { eq: "modules/facebook.svg" }) {
                    publicURL
                    name
                }
                github: file(relativePath: { eq: "modules/github.svg" }) {
                    publicURL
                    name
                }
                twitter: file(relativePath: { eq: "modules/twitter.svg" }) {
                    publicURL
                    name
                }
            }
        `
    }
    render={data => (
        <div id="socials">
            <div>
                <a href="https://instagram.com/georgepestell_"><img src={data.instagram.publicURL} alt={data.instagram.name} /></a>
                <a href="https://facebook.com/georgepestell"><img src={data.facebook.publicURL} alt={data.facebook.name} /></a>
                <a href="https://twitter.com/georgepestell_"><img src={data.twitter.publicURL} alt={data.twitter.name} /></a>
                <a href="https://github.com/georgepestell"><img src={data.github.publicURL} alt={data.github.name} /></a>
            </div>
        </div>
    )} 
    />
)

export default Socials
