import { Link } from "gatsby"
import React from "react"

import Menu from "./Menu"

class Header extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      burgerOpen: false,
    }

    this.handleBurgerClick = this.handleBurgerClick.bind(this)

  }

  handleBurgerClick() {
    this.setState({
      burgerOpen: !this.state.burgerOpen,
    })
  }

  render() {
    return (
      <header>
        <h1>
          <Link to="/">
            GEORGE PESTELL.
          </Link>
        </h1>
        <div className="burger">
          <div className={this.state.burgerOpen ? "open" : ""} onClick={this.handleBurgerClick}>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
        <Menu isOpen={this.state.burgerOpen}/>
    </header>
    ) 
  }
}

export default Header
